import React from "react";
import {Paginator} from "../Paginator/Paginator";
import UsersList from "./UsersList";
import Preloader from "../Preloader/Preloader";

let MyUsers = (props) => {



    return <div> <div>–í—Å–µ–≥–æ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª–µ–π: {props.totalUsersCount}</div>
-----------------------------------
<div>–ü–æ–∫–∞–∑–∞–Ω–æ {props.pageSize} –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª–µ–π</div>
-----------------------------------
        <div>–í—ã –Ω–∞ {props.currentPage}-–æ–π —Å—Ç—Ä–∞–Ω–∏—Ü–µ</div>
-----------------------------------
        <Paginator currentPage={props.currentPage} onPageChanged={props.onPageChanged} totalUsersCount={props.totalUsersCount} pageSize={props.pageSize}/>

        {props.isFetching ? <Preloader /> : <UsersList users={props.users}
                   followingInProgress={props.followingInProgress}
                   followThunk={props.followThunk}
                   unfollowThunk={props.unfollowThunk}/>}
    </div>

}
export default MyUsers